import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {environment} from "../../../../environments/environment";
import {Observable} from "rxjs/Observable";

export interface CharacteristicsForImprovementPlanItemsSelector {
  OID: number;
  Title: string;
  Count: number;
}

export class CharacteristicCaptureList {
  Actions: string;
  CharacteristicOid: number;
  DueDate: any;
  ImprovementPlanGuidance: number;
  ImprovementPlanItemOid: number;
  ImprovementPlanQuestion: number;
  IsCompleted: boolean;
  QuestionGroup: string;
  QuestionOid: number;
  Resposible: string;
  Result: string;
  IsDisabled: boolean;
  IsOverDue?: boolean;
}

@Injectable()
export class MyActionsCaptureService {

  constructor(private httpClient: HttpClient) { }

  getCharacteristicCaptureList(characteristicOid): Observable<any>{
   return this.httpClient.get(`${environment .apiUrl}/api/ImprovementPlanItems/GetCharacteristicCaptureList(${characteristicOid})`)
  }

  getCharacteristics(): Observable<any> {
    return this.httpClient.get(`${environment.apiUrl}/api/ImprovementPlanItems/GetCharacteristics`)
  }

  getCharacteristicCaptureListOverdue(characteristicOid) {
    return this.httpClient.get(`${environment.apiUrl}/api/ImprovementPlanItems/GetCharacteristicCaptureListOverdue(${characteristicOid})`)
  }

  getCharacteristicCaptureListThisMonth(characteristicOid) {
    return this.httpClient.get(`${environment.apiUrl}/api/ImprovementPlanItems/GetCharacteristicCaptureListThisMonth(${characteristicOid})`)
  }
  getCharacteristicCaptureListThisYear(characteristicOid) {
    return this.httpClient.get(`${environment.apiUrl}/api/ImprovementPlanItems/GetCharacteristicCaptureListThisYear(${characteristicOid})`)
  }

  getCharacteristicCaptureListThisWeek(characteristicOid) {
    return this.httpClient.get(`${environment.apiUrl}/api/ImprovementPlanItems/GetCharacteristicCaptureListThisWeek(${characteristicOid})`)
  }

  getCharacteristicCaptureListWithFilter(type, characteristicOid) {
    if(type === 'ALL') {
      return this.getCharacteristicCaptureList(characteristicOid)
    }
    if(type === 'OVERDUE') {
      return this.getCharacteristicCaptureListOverdue(characteristicOid)
    }
    if (type === 'THIS WEEK') {
      return this.getCharacteristicCaptureListThisWeek(characteristicOid)
    }
    if(type === 'THIS MONTH') {
      return this.getCharacteristicCaptureListThisMonth(characteristicOid)
    }
    if(type === 'THIS YEAR') {
      return this.getCharacteristicCaptureListThisYear(characteristicOid)
    }
  }
  createOrUpdateRow(body) {
    return this.httpClient.post(`${environment.apiUrl}/api/ImprovementPlanItems/CreateOrUpdate`, body, {observe: 'response'})
  }

  deleteImprovementPlanItems(key) {
    return this.httpClient.delete(`${environment.apiUrl}/api/ImprovementPlanItems(${key})`)
  }
}



