import {CUSTOM_ELEMENTS_SCHEMA, NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import {
  DevExtremeModule,
  DxButtonModule, DxCheckBoxModule, DxDataGridModule, DxFormModule, DxNumberBoxModule, DxResponsiveBoxModule,
  DxSelectBoxModule, DxTagBoxModule, DxTemplateModule, DxTooltipModule
} from 'devextreme-angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RecaptchaModule } from 'ng-recaptcha';
import { RecaptchaFormsModule } from 'ng-recaptcha/forms';
import { HttpModule } from '@angular/http';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
import { AgmCoreModule } from '@agm/core';
import {TokenInterceptor} from "./services/token.Interseptor";
import { CharacteristicStatusComponent } from './characteristic-status/characteristic-status.component';

@NgModule({
  imports: [
    CommonModule,
    DevExtremeModule,
    DxResponsiveBoxModule,
    DxCheckBoxModule,
    DxSelectBoxModule,
    DxNumberBoxModule,
    DxButtonModule,
    DxFormModule,
    DxTagBoxModule,
    FormsModule,
    DxDataGridModule,
    DxTemplateModule,
    DxTooltipModule,
    RecaptchaModule.forRoot(),
    RecaptchaFormsModule,
    ReactiveFormsModule,
    HttpModule,
    HttpClientModule,
    Ng2PageScrollModule,
    AgmCoreModule.forRoot({
      apiKey: "AIzaSyBkoVvbeNw1T22g7V95635zS16NQo5dYNs",
      libraries: ["places"]
    })
  ],
  declarations: [],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true,
    }
  ],
  exports: [
    CommonModule,
    DevExtremeModule,
    DxResponsiveBoxModule,
    DxCheckBoxModule,
    DxSelectBoxModule,
    DxNumberBoxModule,
    DxButtonModule,
    DxFormModule,
    DxTagBoxModule,
    FormsModule,
    DxDataGridModule,
    DxTemplateModule,
    DxTooltipModule,
    ReactiveFormsModule,
    AgmCoreModule,
    RecaptchaModule,
    RecaptchaFormsModule,
    Ng2PageScrollModule,

  ],
  entryComponents: [

  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA]
})
export class SharedModule { }



