import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProfileRoutingModule } from './user-profile.routing.module';
import { UserProfileComponent } from './user-profile.component';
import { UserProfileInfoComponent } from './user-profile-info/user-profile-info.component';
import { UserProfileOrganisationComponent } from './user-profile-organisation/user-profile-organisation.component';
import { UserProfileBusinessUnitComponent } from './user-profile-business-unit/user-profile-business-unit.component';
import { UserProfileSubscriptionComponent } from './user-profile-subscription/user-profile-subscription.component';
import { UserProfileBusinessUnitItemComponent } from './user-profile-business-unit-item/user-profile-business-unit-item.component';
import { UserProfileService } from './user-profile.service';
import { UserProfileInfoService } from './user-profile-info/user-profile-info.service';
import { UserProfileOrganisationService } from './user-profile-organisation/user-profile-organisation.service';
import { UserProfileBusinessUnitItemService } from './user-profile-business-unit-item/user-profile-business-unit-item.service';
import { UserProfileSubscriptionService } from './user-profile-subscription/user-profile-subscription.service';
import { SharedModule } from '../shared/shared.module';
import { CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';

@NgModule({
  imports: [
    CommonModule,
    ProfileRoutingModule,
    SharedModule
  ],
  declarations: [
  UserProfileComponent,
  UserProfileInfoComponent,
  UserProfileOrganisationComponent,
  UserProfileBusinessUnitComponent,
  UserProfileSubscriptionComponent,
  UserProfileBusinessUnitItemComponent,
],
  providers: [
    UserProfileService,
    UserProfileInfoService,
    UserProfileOrganisationService,
    UserProfileBusinessUnitItemService,
    UserProfileSubscriptionService,
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class UserProfileModule{}





