import {
  AfterViewChecked,
  ChangeDetectorRef,
  Component,
  ElementRef,
  NgZone,
  OnInit,
  ViewChild
} from '@angular/core';
import {Observable} from "rxjs/Observable";
import {ActivatedRoute, Router} from "@angular/router";
import {UserProfileService} from "../user-profile.service";
import {UnitInfo, UnitStructure, UnitType} from "../../shared/models/unit.model";
import {RegistrationSettingsService} from "../../authentification/registration-settings/registration-settings.service";
import {FormValidation} from "../../shared/models/form-validation.model";
import {UserService} from "../../shared/services/user.service";
import {MapsAPILoader} from "@agm/core";
import {FormControl} from "@angular/forms";
import {} from 'googlemaps';
import {UnitService} from "../../shared/services/unit.service";
import {Commodity} from "../../shared/models/commodity.model";
import {CommodityService} from "../../shared/services/commodity.service";
import {Supplier} from "../../shared/models/supplier.model";
import {QuestionsService} from "../../shared/services/questions.service";
import 'rxjs/Rx';
import {AnswerToPost} from "../../shared/models/question.model";
import {UserProfileBusinessUnitItemService} from "./user-profile-business-unit-item.service";
import {DxTagBoxComponent} from "devextreme-angular";

@Component({
  selector: 'bluenorth-user-profile-business-unit-item',
  templateUrl: './user-profile-business-unit-item.component.html',
  styleUrls: ['./user-profile-business-unit-item.component.scss']
})
export class UserProfileBusinessUnitItemComponent implements OnInit, AfterViewChecked {

  @ViewChild("search")
  public searchElementRef: ElementRef;

  @ViewChild('multySelector')
  public multySelector: DxTagBoxComponent;

  @ViewChild('multySelectorSupplier')
  public multySelectorSupplier: DxTagBoxComponent;

  @ViewChild('multySelectorRetailer')
  public multySelectorRetailer: DxTagBoxComponent;

  public currentUnitId: string;
  public latitude: number;
  public longitude: number;
  public searchControl: FormControl;
  public zoom: number;
  public isCommodityPopupVisible = false;
  public isSupplierPopupVisible = false;
  public unitAddress: string = 'HI';
  public userId;
  public standardsOidArray = [];
  public ifFrameworkSelected = false;
  public phoneCode: string = '';
  organisationOid;
  isUnitCreate = false;
  formValidation: FormValidation;
  phonePattern: any;
  emailPattern: any;
  phoneRules: any;
  bussinesUnitInfo: UnitType[] = [];

  businessUnitStructure: any[] = [];
  unitInfo: UnitInfo = {};
  newCommodity: Commodity;
  newSupplier: Supplier;
  countriesInfo: any[] = [];
  commodityInfo: any[] = [];
  supplierInfo: any[] = [];
  frameworkInfo: any[] = [];
  standardsInfo: any[] = [];
  retailerInfo: any[] = [];
  currentStandards: any[] = [];

  public countryBoxOptions: {
    dataSource: any[],
    displayExpr: 'Name',
    valueExpr: 'Oid',
    placeholder: "SELECT COUNTRY",
    value: string

  };
  public unitTypeBoxOptions: {
    dataSource: any[],
    displayExpr: 'Name',
    valueExpr: 'OID',
    placeholder: "ADD TYPE OF BUSINESS UNIT",
    value: any
  };

  public commoditiesBoxOptions: {
    dataSource: any[],
    displayExpr: 'Title',
    valueExpr: 'OID',
    placeholder: "SELECT PRIMARY COMMODITY",
    value: any,
  };

  public frameworkBoxOptions: {
    items: any[],
    displayExpr: 'Title',
    valueExpr: 'OID',
    placeholder: "SELECT PRIMARY FRAMEWORK",
    value?: any,
    disabled: boolean;
  };
  public unitStructureBoxOptions: {
    dataSource: any[],
    displayExpr: 'Name',
    valueExpr: 'Oid',
    placeholder: "ADD BUSINESS UNIT STRUCTURE",
    value: any
  };
  public supplierBoxOptions: {
    dataSource: any[],
    displayExpr: 'Title',
    valueExpr: 'OID',
    placeholder: "SELECT SUPPLIER",
    value: any
  };

  public retailerBoxOptions: {
    dataSource: any[],
    displayExpr: 'Title',
    valueExpr: 'OID',
    placeholder: "SELECT SUPPLIER",
    value: any
  };

  public questions: any[] = [];
  public questionsWithAnswers: any[] = [];
  public answersToPost: AnswerToPost[] = [];

  self = this;
  constructor(private route: ActivatedRoute,
              private router: Router,
              private userProfileService: UserProfileService,
              private registrationSettingsService: RegistrationSettingsService,
              private userService: UserService,
              private commodityService: CommodityService,
              private unitService: UnitService,
              private mapsAPILoader: MapsAPILoader,
              private questionsService: QuestionsService,
              private userProfileBusinessUnitItemService: UserProfileBusinessUnitItemService,
              private changeDetector: ChangeDetectorRef,
              private ngZone: NgZone) {
    this.formValidation = new FormValidation();
    this.phonePattern = this.formValidation.getPhonePattern();
    this.phoneRules = this.formValidation.getPhoneRules();
    this.emailPattern = this.formValidation.getEmailPattern();
    this.newCommodity = this.userProfileService.getNewCommodity();
    this.newSupplier = this.userProfileService.getNewSupplier();
    this.getAddressFromCordinates = this.getAddressFromCordinates.bind(this)
    this.markerDragEnd = this.markerDragEnd.bind(this)
    this.unitAddress = this.self.unitAddress;
  }

  ngOnInit() {
    this.currentStandards = [];
    this.standardsOidArray = [];
    this.getCurrentUnitId()
      .subscribe((unitId: string) => {
        if (unitId !== "new-unit") {
          this.unitService.getUnitById(unitId)
            .subscribe(unit => {
              this.unitInfo = unit;
              console.log('this.unitInfo GET', this.unitInfo);
              this.getAddressFromCordinates(this.unitInfo.LocationLattitude, this.unitInfo.LocationLongtitude);
              console.log('this.unitInfo', this.unitInfo, this.unitAddress);
              this.setupGoogleMap();
              // console.log('this.searchControl', this.searchControl, this.unitAddress);

          this.userProfileBusinessUnitItemService.getStandardsByUnitId(unitId.toString())
            .subscribe((standards: any) => {
              console.log('getStandardsByUnitID', standards);
              this.currentStandards = standards;
            })
            })
        } else {
          this.isUnitCreate = true;
          this.setupGoogleMap();
          this.latitude = 39.8282;
          this.longitude = -98.5795;
          this.userService.getUserInfo()
            .subscribe(userLight => {
              this.userId = userLight.body['Oid'];
              console.log('userLight', userLight.body);
              if (userLight.body['OrganisationOid'] !== '') {
                this.organisationOid = userLight.body['OrganisationOid'];
                this.unitInfo.OrganisationId = this.organisationOid;
              }
            });
        }
      });
    this.commodityService.getCommodities()
      .subscribe((response: any) => {
          console.log('commodities', response);
          this.commodityInfo = response;
          let commodityValue = this.isUnitCreate ? (this.commodityInfo.length === 1 ? this.commodityInfo[0].OID : "") : this.unitInfo.UnitCommodityId;
          this.commoditiesBoxOptions = {
            dataSource: this.commodityInfo,
            displayExpr: 'Title',
            valueExpr: 'OID',
            value: commodityValue,
            placeholder: "SELECT PRIMARY COMMODITY"

          };
        },
        error => {
          console.log("commodities", error),
            this.userService.showUserNotification(error.error.Message, 'error');
        });

    this.businessUnitStructure = [{Name: 'Independent', Oid: UnitStructure.Independent}, {
      Name: 'Cooperative',
      Oid: UnitStructure.Cooperative
    }];
    let businessUnitStructureValue = this.isUnitCreate ? (this.businessUnitStructure.length === 1 ? this.businessUnitStructure[0].Oid : "") : this.unitInfo.UnitStructure;
    this.unitStructureBoxOptions = {
      dataSource: this.businessUnitStructure,
      displayExpr: 'Name',
      valueExpr: 'Oid',
      placeholder: "ADD BUSINESS UNIT STRUCTURE",
      value: businessUnitStructureValue,
    };

    this.registrationSettingsService.getUnitType()
      .subscribe((response) => {
          this.bussinesUnitInfo = response['value'];
          let unitTypeValue = this.isUnitCreate ? (this.bussinesUnitInfo.length === 1 ? this.bussinesUnitInfo[0].OID : "") : this.unitInfo.UnitTypeId;
          this.unitTypeBoxOptions = {
            dataSource: this.bussinesUnitInfo,
            displayExpr: 'Name',
            valueExpr: 'OID',
            value: unitTypeValue,
            placeholder: "ADD TYPE OF BUSINESS UNIT"
          };

        }
        , error => {
          this.userService.showUserNotification(error.error.Message, 'error');
        });
    this.userService.getCountriesList()
      .subscribe((item: any[]) => {
        this.countriesInfo = item;
        console.log('this.countriesInfo', this.countriesInfo);
        let countriesValue = this.isUnitCreate ? (this.countriesInfo.length === 1 ? this.countriesInfo[0].Oid : "") : this.unitInfo.UnitCountryId;

        this.countryBoxOptions = {
          dataSource: this.countriesInfo,
          displayExpr: 'Name',
          valueExpr: 'Oid',
          value: countriesValue,
          placeholder: "SELECT COUNTRY"
        };
      });

    this.registrationSettingsService.getSuppliers()
      .subscribe((response: any[]) => {
        console.log('suppliers', response);
        this.supplierInfo = response;
       /* let suppliersValue = this.isUnitCreate ? (this.supplierInfo.length === 1 ? this.supplierInfo[0].OID : "") : this.unitInfo.UnitSupplierId;
        this.supplierBoxOptions = {
          dataSource: this.supplierInfo,
          displayExpr: 'Title',
          valueExpr: 'OID',
          value: suppliersValue,
          placeholder: "SELECT SUPPLIER"
        };*/
      });

    this.registrationSettingsService.getRetailers()
      .subscribe((response: any[]) => {
      this.retailerInfo = response;
    })

    this.questionsService.getQuestionsByGroup('scale')
      .subscribe((questions: any[]) => {
          console.log('questions', questions);
          this.questions = questions;
          this.questions.map(question => {
            let questionItem = {
              questionId: question.OID,
              question: question.Title,
              answer: null
            };
            this.questionsWithAnswers.push(questionItem);
          });
          console.log('this.questionsWithAnswers', this.questionsWithAnswers);
        },
        error => {
          this.userService.showUserNotification(error.error.Message, 'error');
        });


  }

  getCountryValueForUnit(value) {
    this.unitInfo.UnitCountryId = value.selectedItem.Oid
    this.phoneCode = value.selectedItem.PhoneCode ? value.selectedItem.PhoneCode : "";
  }

  private getCurrentUnitId(): Observable<string> {
    return this.route.params.mergeMap(params => {
      this.currentUnitId = params['unitId'].toString();
      return Observable.of(this.currentUnitId);
    });
  }

  onCommodityChanged(e) {
    console.log(e)
  }

  setupGoogleMap() {
    //set google maps defaults
    this.zoom = 4;
    this.latitude = this.unitInfo.LocationLattitude;
    this.longitude = this.unitInfo.LocationLongtitude;
    //create search FormControl
    //this.searchControl = new FormControl();
    //set current position
    this.setCurrentPosition();
    console.log('this.searchElementRef.nativeElement')
    //load Places Autocomplete

    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
        types: ["address"]
      });
      console.log(autocomplete);
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();

          //verify result
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }
          //set latitude, longitude and zoom
          this.latitude = place.geometry.location.lat();
          this.longitude = place.geometry.location.lng();
          this.zoom = 12;
        });
      });
    });
  }

  private setCurrentPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 12;
        console.log(this.latitude);
        console.log(this.longitude);

      });
    }
  }

  onFormSubmitUpdateUnit(e) {
    let counter = 0;
    if (this.isUnitCreate) {
      console.log("this.unitInfo", this.unitInfo);
      console.log("this.userService.getCurrentUser().userId,", this.userService.getCurrentUser().userId);
      /* this.questionsWithAnswers.forEach(question => {
         if (question.answer === null) {
           this.userService.showUserNotification('You need to answer all questions', 'info');
           return;
         } else {
           counter++;
         }
       });*/
    }

    this.unitInfo.LocationLattitude = this.latitude;
    this.unitInfo.LocationLongtitude = this.longitude;
    this.unitInfo.Suppliers = this.multySelectorSupplier.value;
    this.unitInfo.Retailers = this.multySelectorRetailer.value;
    this.unitInfo.Standards = this.multySelector.value;
    console.log('this.multySelectorRetailer',  this.multySelectorRetailer);
    console.log('this.multySelectorSupplier', this.multySelectorSupplier);
    if (!this.isUnitCreate) {
      let isStandardUpdated = false;
      console.log('this.unitInfo to update', this.unitInfo)
      console.log('this.standardsOidArray', this.standardsOidArray)
      this.registrationSettingsService.addOrUpdateUnit(this.unitInfo)
        .subscribe(unitUpdate => {
            let message = !this.isUnitCreate ? `UNIT ${this.unitInfo.UnitName.toUpperCase()} WAS UPDATED` : `UNIT ${this.unitInfo.UnitName.toUpperCase()} WAS CREATED`;
            this.userService.showUserNotification(message, 'success');
            console.log("unitUpdate/create", unitUpdate);
              this.router.navigate([`profile/business-unit`]);

          },
          error => {
            this.userService.showUserNotification(`${error.error.Message}`, 'error');
          });

console.log('isStandardUpdated', isStandardUpdated)
    }
    /* if (this.isUnitCreate && counter === this.questionsWithAnswers.length) {*/
    if(this.isUnitCreate) {
      this.registrationSettingsService.addOrUpdateUnit(this.unitInfo)
        .subscribe(unitUpdate => {
            let isStandardUpdated = false;
            console.log('this.questionsWithAnswers', this.questionsWithAnswers);
            /* this.questionsWithAnswers.map(answer => {
               this.answersToPost.push({
                 Unit: unitUpdate['Oid'],
                 User: this.userId,
                 AnswerText: answer.answer,
                 Question: answer.questionId
               });
             });*/

            console.log('this.answersToPost', this.answersToPost);
            /* this.questionsService.addAnswersArray(this.answersToPost)
               .subscribe(answers => {
                 this.questionsWithAnswers = [];
                 console.log('answers', answers);*/
            // if (answers.status === 200) {
              let message = !this.isUnitCreate ? `UNIT ${this.unitInfo.UnitName.toUpperCase()} WAS UPDATED` : `UNIT ${this.unitInfo.UnitName.toUpperCase()} WAS CREATED`;
              this.userService.showUserNotification(message, 'success');
              console.log("unitUpdate/create", unitUpdate);
              this.router.navigate([`profile/business-unit`])

            // }
          },
          error => {
            this.userService.showUserNotification(`${error.error.Message}`, 'error');
          });
    }
    //   });
    //  }
    e.preventDefault();
  }

  public getAddressFromCordinates(lat, lng) {
    let geocoder = new google.maps.Geocoder();
    let latlng = new google.maps.LatLng(lat, lng);
    geocoder.geocode({
      'location': latlng
    },((results, status) => {
      if (status === google.maps.GeocoderStatus.OK) {
        if (results[1]) {
          this.unitAddress = results[1].formatted_address;
          this.searchElementRef.nativeElement.value =  this.unitAddress;
          console.log('ADDRES', this.unitAddress);
          if (this.unitAddress) {
           // this.searchElementRef = new FormControl({value: this.unitAddress});
           // console.log('this.searchElementRef', this.searchElementRef.value);
            console.log('this.searchElementRef', this.searchElementRef);
          }
        } else {
          console.log('no results');
        }
      } else {
        console.error('Geocoder failed due to: ' + status);
      }
    })
    );
  }

  onFormSubmitCreateCommodity(e) {
    console.log('newCommodity', this.newCommodity);
    this.commodityService.addCommodity(this.newCommodity)
      .subscribe(commodity => {
          delete commodity['@odata.context'];
          this.commodityInfo.push(commodity);
          this.commoditiesBoxOptions = {
            dataSource: this.commodityInfo,
            displayExpr: 'Title',
            valueExpr: 'OID',
            value: commodity['OID'],
            placeholder: "SELECT PRIMARY COMMODITY"
          };
          console.log('commodity', commodity);
          console.log('this.commodityInfo', this.commodityInfo);
          this.userService.showUserNotification(`COMMODITY ${this.newCommodity.Title.toUpperCase()} WAS CREATED`, 'success');
          this.isCommodityPopupVisible = false;
        },
        error => {
          this.userService.showUserNotification(`${error.error.Message}`, 'error');
        });
    e.preventDefault();
  }

  onFormSubmitCreateSupplier(e) {
    console.log('this.newSupplier', this.newSupplier);
    this.registrationSettingsService.addSupplier(this.newSupplier)
      .subscribe(supplier => {
          this.supplierInfo.push(supplier);
          this.supplierBoxOptions = {
            dataSource: this.supplierInfo,
            displayExpr: 'Title',
            valueExpr: 'OID',
            value: supplier['OID'],
            placeholder: "SELECT SUPPLIER"
          };
          this.userService.showUserNotification(`SUPPLIER ${this.newSupplier.Title.toUpperCase()} WAS CREATED`, 'success');
          this.isSupplierPopupVisible = false;
          console.log('NEWsupplier', supplier);
        },
        error => {
          this.userService.showUserNotification(`${error.error.Message}`, 'error');
        });
    e.preventDefault();
  }

  onFieldDataChanged(event) {
    console.log('field', event)
    if (event.dataField === "UnitCommodityId" && event.value) {
      this.userProfileBusinessUnitItemService.getFrameworksByCommodity(event.value)
        .subscribe((frameworks: any) => {
            this.frameworkInfo = frameworks;
            console.log('farmeworks', frameworks);
            if (this.frameworkInfo.length) {
              this.frameworkBoxOptions = {
                items: this.frameworkInfo,
                displayExpr: 'Title',
                valueExpr: 'OID',
                placeholder: "SELECT PRIMARY FRAMEWORK",
                disabled: false,
              };
            }
          },
          error => {
            this.userService.showUserNotification(error.error.Message, 'error')
          })
    }
    if (event.dataField === "UnitCommodityId" && !event.value) {
      this.frameworkBoxOptions = {
        items: this.frameworkInfo,
        displayExpr: 'Title',
        valueExpr: 'OID',
        placeholder: "SELECT PRIMARY FRAMEWORK",
        disabled: true
      };
    }
    if (event.dataField === "FrameworkId" && event.value) {
      this.userProfileBusinessUnitItemService.getStandardsByFramework(event.value)
        .subscribe((standards: any) => {
          console.log('this.standardsInfo', standards);
          this.standardsInfo = standards;
          this.ifFrameworkSelected = true;
        })

    }
  }

  markerDragEnd(event) {
    console.log('MARKER', event)
    this.getAddressFromCordinates(event.coords.lat, event.coords.lng)
    this.latitude = event.coords.lat;
    this.longitude =  event.coords.lng;
    }

  setStandards(event) {
    console.log('changeStandards', event.value)
    this.standardsOidArray = [];
    if (this.multySelector) {
      console.log(this.multySelector)
      this.standardsOidArray = this.multySelector.selectedItems.map(item => {
        return item.OID;
      });
    }
    console.log(this.standardsOidArray);
  }

 /* setSupplier(event) {
    console.log('supplier', this.multySelectorSupplier.values);
  }

  setRetailer(event) {
    console.log('retailer', this.multySelectorRetailer.values)
  }
*/
  ngAfterViewChecked() {
    this.changeDetector.detectChanges();
  }
}



