import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { RegistrationComponent } from './authentification/registration/registration.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './authentification/login/login.component';
import { DashboardComponent } from './home/dashboard/dashboard.component';
import { AuthGuard } from './shared/services/auth-guard.service';
import { ForgotPasswordComponent } from './authentification/forgot-password/forgot-password.component';
import { TermsConditionsComponent } from './authentification/terms-conditions/terms-conditions.component';
import { RegistrationSettingsComponent } from './authentification/registration-settings/registration-settings.component';
import { StandardsComponent } from "./home/standards/standards.component";
import { AuthResolverService } from "./shared/services/auth-resolver.service";
import { AssessmentCurrentStatusComponent } from "./home/assessment/assessment-current-status/assessment-current-status.component";
import { AssessmentStateComponent } from "./home/assessment/assessment-state/assessment-state.component";
import { AssessmentCaptureFullComponent } from "./home/assessment/assessment-capture-full/assessment-capture-full.component";
import { AssessmentCaptureLightComponent } from "./home/assessment/assessment-capture-light/assessment-capture-light.component";
import { IndicatorDataSetComponent } from "./home/performance-indicators/indicator-data-set/indicator-data-set.component";
import { IndicatorDataSetValueComponent } from "./home/performance-indicators/indicator-data-set-value/indicator-data-set-value.component";
import { AssessmentReportingComponent } from "./home/assessment/assessment-reporting/assessment-reporting.component";
import { IndicatorReportingComponent } from "./home/performance-indicators/indicator-reporting/indicator-reporting.component";
import {MyActionsCaptureComponent} from "./home/my-actions/capture/capture.component";
import {MyActionsCurrentStatusComponent} from "./home/my-actions/current-status/current-status.component";
import {MyActionsPrioritiseComponent} from "./home/my-actions/prioritise/prioritise.component";
import {MyActionsReportingComponent} from "./home/my-actions/reporting/reporting.component";


const appRoutes = [
  {
    path: "",
    component: LoginComponent
  },
  {
    path: "login",
    component: LoginComponent
  },
  {
    path: "registration",
    component: RegistrationComponent
  },
  {
    path: "terms-and-conditions",
    component: TermsConditionsComponent
  },
  {
    path: "registration-settings",
    component: RegistrationSettingsComponent
  },
  {
    path: 'units',
    canActivate: [AuthGuard],
    component: RegistrationSettingsComponent,
  },

  {
    path: "forgot-password",
    component: ForgotPasswordComponent
  },
  {
    path: "",
    canActivate: [AuthGuard],
    resolve: {
      currentUser: AuthResolverService
    },
    children: [
      {
        path: "",
        component: HomeComponent,
        children: [
          {
            path: "",
            canActivateChild: [AuthGuard],
            children: [
              {
                path: "",
                component: DashboardComponent,
              },
              {
                path: "dashboard",
                component: DashboardComponent,
                data: { title: 'DASHBOARD' }
              },
              {
                path: "assessment-current-status-init",
                component: AssessmentCurrentStatusComponent,
                data: { title: 'STRATEGIC ASSESSMENT'}
              }, {
                path: "assessment-current-status",
                component: AssessmentStateComponent,
                data: { title: 'STRATEGIC ASSESSMENT'}
              },
              {
                path: "assessment-capture-full",
                component: AssessmentCaptureFullComponent,
                data: {title: 'STRATEGIC ASSESSMENT'}
              },
              {
                path: "assessment-capture-light",
                component: AssessmentCaptureLightComponent,
                data: { title: 'STRATEGIC ASSESSMENT', subtitle: 'lite assessment'}
              },
              {
                path: "assessment-reporting",
                component: AssessmentReportingComponent,
                data: { title: 'STRATEGIC ASSESSMENT', subtitle: 'reporting'}
              },
              {
                path: "standards-management",
                component: StandardsComponent,
                data: { title: 'STANDARDS' , subtitle: 'current status'}
              },
              {
                path: "performance-indicators-current-status",
                component: IndicatorDataSetComponent,
                data: { title: 'PERFORMANCE INDICATORS' }
              },
              {
                path: "performance-indicators-capture",
                component: IndicatorDataSetValueComponent,
                data: { title: 'PERFORMANCE INDICATORS' }
              },
              {
                path: "performance-indicators-reporting",
                component: IndicatorReportingComponent,
                data: { title: 'PERFORMANCE INDICATORS' }
              },
              {
                path: "standards-management/:standardId",
                component: IndicatorDataSetComponent,
                data: {title: 'INDICATOR DATA SETS'}
              },
              {
                path: "standards-management/:standardId/performance-indicators",
                component: IndicatorDataSetValueComponent,
                data: {title: 'PERFORMANCE INDICATORS'}
              },
              {
                 path: "standards-management/:standardId/:indicatorDataSetId",
                 component: IndicatorDataSetValueComponent,
                data: {title: 'PERFORMANCE INDICATORS'}
              },
              {
                path: "my-actions-current-status",
                component: MyActionsCurrentStatusComponent,
                data: {title: 'MY ACTIONS'}
              },
              {
                path: "my-actions-prioritise",
                component: MyActionsPrioritiseComponent,
                data: {title: 'MY ACTIONS'}
              },
              {
                path: "my-actions-capture",
                component: MyActionsCaptureComponent,
                data: {title: 'MY ACTIONS'}
              },
              {
                path: "my-actions-reporting",
                component: MyActionsReportingComponent,
                data: {title: 'MY ACTIONS'}
              }
            ],
          }
        ]
      },
      {
        path: "profile",
        loadChildren: 'app/user-profile/user-profile.module#UserProfileModule'
      },

    ]
  },
  { path: '**', redirectTo: 'login' }
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class BluenorthRoutingModule { }



