import {Component, ElementRef, OnInit, ViewChild} from "@angular/core";

import {DxDataGridComponent, DxSelectBoxComponent, DxTagBoxComponent} from "devextreme-angular";

import {IndicatorDataSetValueService} from "./indicator-data-set-value.service";
import {PerformanceIndicator} from "../../../shared/models/performanseIndicator.model";
import {StandardsService} from "../../standards/standards.service";
import {UserService} from "../../../shared/services/user.service";

export interface Characteristic {
  OID: any;
  Title: string;
}

@Component({
  selector: 'bluenorth-indicator-data-set-value',
  templateUrl: './indicator-data-set-value.component.html',
  styleUrls: ['./indicator-data-set-value.component.scss']
})
export class IndicatorDataSetValueComponent implements OnInit {

  @ViewChild('dataTable') dataTable: DxDataGridComponent;
  @ViewChild('multySelector') multySelector: DxTagBoxComponent;
  @ViewChild('multySelectorForReview') multySelectorForReview: DxTagBoxComponent;
  @ViewChild('unitOMSelector') unitOMSelector: DxSelectBoxComponent;
  @ViewChild('templateContainer')
  private templateContainer: ElementRef;

  public currentUnitId: string;
  public currentStandardId: number;
  public characteristicList = [];
  public indicatorDataSetList: any[] = [];
  public selectedCharacteristic: string;
  public tooltipVisible = false;
  public selectedDatasets = [];
  public selectedDatasetsForReview = [];
  public selectedCharacteristicOID: number = null;
  public testArray1 = [];
  public buttonTextNotes = 'NOTES';
  public isNotesPopupVisible = false;
  public notes: any[] = [];
  public value = '';
  public currentUserFullName: string;
  public isIndicatorPopupVisible = false;
  public isSubmitForReviewPopupVisible = false;
  public indicatorGuidance: any = {};


  constructor(private standardsService: StandardsService,
              private indicatorDataSetValueService: IndicatorDataSetValueService,
              private userService: UserService) {}

  ngOnInit() {
    this.userService.getUserInfo()
      .subscribe(user => {
        this.dataTable.instance.beginCustomLoading('Loading..');
        this.currentUserFullName = `${user.body['FirstName']} ${user.body['LastName']}`;
        });
    this.standardsService.getCharacteristicsByStandard()
      .subscribe((characteristics: any[]) => {
        this.currentStandardId = this.standardsService.currentStandardId;
        characteristics.map(characteristic => {
          this.characteristicList.push({
            OID: characteristic['OID'],
            Title: characteristic['Title']
          });
          this.selectedCharacteristic = this.characteristicList[0].Title;
          this.selectedCharacteristicOID = this.characteristicList[0].OID;
        });
      });

    this.standardsService.getIndicatorDataSetListByStandardOidFromUnit()
      .subscribe((indicatorDataSetList: any) => {
        console.log("indicatorDataSetList", indicatorDataSetList);
        indicatorDataSetList.map(indicator => {
          this.indicatorDataSetList.push({
            PeriodFrom: `${indicator.PeriodFromMonth < 10 ? '0'+ indicator.PeriodFromMonth : indicator.PeriodFromMonth}-${indicator.PeriodFromYear}/${indicator.PeriodToMonth < 10 ? '0' + indicator.PeriodToMonth : indicator.PeriodToMonth}-${indicator.PeriodToYear}`,
            OID: indicator.OID
          });
        });
        console.log('this.indicatorDataSetList', this.indicatorDataSetList)
      })
  }

  onToolbarPreparing(e) {
    console.log('onToolbarPreparing', e.toolbarOptions)
    let indexRevertButton = e.toolbarOptions.items.indexOf(e.toolbarOptions.items.find(function (item) {
      return item.name == "revertButton";
    }));
    if (indexRevertButton != -1) {
      e.toolbarOptions.items.splice(indexRevertButton, 1);
    }
  }

  setCurrentCharacteristic(value) {
    console.log(" setCurrentCharacteristic", value)
    let dataSetOidArray = [];
    this.selectedDatasets = this.multySelector.selectedItems;
    this.selectedCharacteristic = value.selectedItem.Title;
    this.selectedCharacteristicOID = value.selectedItem.OID;
    for (let i = 0; i < this.selectedDatasets.length; i++) {
      this.dataTable.instance.deleteColumn(this.selectedDatasets[i].OID.toString())
    }

    this.selectedDatasets.map(item => {
      dataSetOidArray.push(item.OID);
    });

    let queryParams = {
      "characteristicOid": value.selectedItem.OID,
      "dataSetsOidList": dataSetOidArray
    };

    this.standardsService.getIndicatorsDataSetByCharacteristicAndDataSetList(queryParams)
      .subscribe((response: any[]) => {
        if (response) {
          for (let i = 0; i < this.selectedDatasets.length; i++) {

            this.dataTable.instance.addColumn({
              caption: this.selectedDatasets[i].PeriodFrom,
              dataField: this.selectedDatasets[i].OID.toString(),
              dataType: "number"
            });
          }

          this.testArray1 = response;
          this.dataTable.instance.endCustomLoading();
          console.log('getIndicatorsDataSetByCharacteristicAndDataSetList', response);
          console.log('8-this.testArray1', this.testArray1);
          for (let i = 0; i < this.testArray1.length; i++) {
            for (let k = 0; k < this.testArray1[i].IndicatorValue.length; k++) {
              if (this.testArray1[i].IndicatorValue[k].Value === null) {
                this.testArray1[i].IndicatorValue[k].DataSetOid = this.selectedDatasets[k].OID;
                this.testArray1[i].IndicatorValue[k].Value = '';
                console.log('this.testArray1[i].IndicatorValue[k].DataSetOid', this.testArray1[i].IndicatorValue[k].DataSetOid);
              }
              this.dataTable.instance.cellValue(i, this.testArray1[i].IndicatorValue[k].DataSetOid.toString(), this.testArray1[i].IndicatorValue[k].Value);
              console.log('9-0', i, this.testArray1[i].IndicatorValue[k].DataSetOid.toString(), this.testArray1[i].IndicatorValue[k].Value);
              console.log('9-this.dataTable', this.dataTable)
            }
          }
        }
      })
  }

  onEditorPreparing(event) {
    if ((event.dataField == "Title" || event.dataField == "UnitOfMeasurerOid") && event.parentType == "dataRow") {
      event.editorOptions.disabled = !event.row.inserted;
    }
  }

  addOrUpdateIndicatorValue(e) {
    let indicatorArray = [];
    let indicatorValue = {};
    let dataSetOidInTable = Object.keys(e.newData);
    console.log('dataSetOidInTable', dataSetOidInTable);
    for (let i = 0; i < this.testArray1.length; i++) {
      if (this.testArray1[i].IndicatorOid === e.key) {
        this.testArray1[i].IndicatorValue.forEach(item => {
          for (let i = 0; i < dataSetOidInTable.length; i++) {
            if (item.DataSetOid.toString() == dataSetOidInTable[i]) {
              let valueOid = item.ValueOid === null ? item.ValueOid = 0 : item.ValueOid;
              indicatorValue = {
                "OID": valueOid,
                "Value": e.newData[dataSetOidInTable[i]],
                "Dataset": item.DataSetOid,
                "Indicator": e.oldData.IndicatorOid,
                "UnitOfMeasure": e.newData.UnitOfMeasurerOid ? e.newData.UnitOfMeasurerOid : e.oldData.UnitOfMeasurerOid
              }
              indicatorArray.push(indicatorValue);
            }
          }
        });
      }
    }
    console.log('indicatorArrayToUpdate', indicatorArray);
    if (indicatorArray.length) {
      this.standardsService.createOrUpdateIndicatorValue(indicatorArray)
        .subscribe(updateIndicatorValue => {
            this.userService.showUserNotification("Values was updated", 'success');
            console.log('updateIndicatorValue', updateIndicatorValue);
            indicatorArray = [];
          },
          error => {
            this.userService.showUserNotification(error.error.Message, 'error')
          });
    }
  }

  toggleDefault(e) {
    /* if(e.eventType === 'mouseenter' || 'mouseleave') {
       this.tooltipVisible = e.rowIndex;
       this.titleContent = e.data.IndicatorOid
     }*/
    //this.tooltipVisible = !this.tooltipVisible;
    // console.log('TOOLTIP', e);
    //console.log('this.tooltipVisible', this.tooltipVisible);
  }

  setCurrentDataSetList(event) {
    if (this.selectedCharacteristicOID) {
      let dataSetOidArray = [];
      this.selectedDatasets = this.multySelector.selectedItems;
      this.selectedDatasets.map(item => {
        dataSetOidArray.push(item.OID);
      });
      let queryParams = {
        "characteristicOid": this.selectedCharacteristicOID,
        "dataSetsOidList": dataSetOidArray
      };
      this.standardsService.getIndicatorsDataSetByCharacteristicAndDataSetList(queryParams)
        .subscribe((response: any[]) => {
          console.log('getIndicatorsDataSetByCharacteristicAndDataSetList', response);
          if (response) {
            if (event.addedItems.length > 0) {
              console.log('6-event.addedItems', event.addedItems)
              console.log(' this.dataTable.instance',  this.dataTable.instance)
              this.dataTable.instance.addColumn({
                caption: event.addedItems[0].PeriodFrom,
                dataField: event.addedItems[0].OID.toString(),
                dataType: "number"
              });
              this.testArray1 = response;
              for (let i = 0; i < this.testArray1.length; i++) {
                for (let k = 0; k < this.testArray1[i].IndicatorValue.length; k++) {
                  if (this.testArray1[i].IndicatorValue[k].Value === null) {
                    this.testArray1[i].IndicatorValue[k].DataSetOid = this.selectedDatasets[k].OID;
                    this.testArray1[i].IndicatorValue[k].Value = '';
                  }
                  this.dataTable.instance.cellValue(i, this.testArray1[i].IndicatorValue[k].DataSetOid.toString(), this.testArray1[i].IndicatorValue[k].Value);
                }
              }
            }
          }
          if (event.removedItems.length > 0) {
            console.log('10-event.removedItems', event.removedItems);
            this.dataTable.instance.deleteColumn(event.removedItems[0].OID.toString())
            this.testArray1 = response;
            for (let i = 0; i < this.testArray1.length; i++) {
              for (let k = 0; k < this.testArray1[i].IndicatorValue.length; k++) {
                if (this.testArray1[i].IndicatorValue[k].Value === null) {
                  this.testArray1[i].IndicatorValue[k].DataSetOid = this.selectedDatasets[k].OID;
                  this.testArray1[i].IndicatorValue[k].Value = '';
                }
                this.dataTable.instance.cellValue(i, this.testArray1[i].IndicatorValue[k].DataSetOid.toString(), this.testArray1[i].IndicatorValue[k].Value);
              }
            }
          }
        })
    }
  }

  onUOMChanged(e, templateInfo) {
    templateInfo.setValue(e.value);
  }

  unitOfMeasureValue(data) {
    let unit: any;
    let result;
    if (data.UnitsOfMeasure !== null) {
      for (let i = 0; i < data.UnitsOfMeasure.length; i++) {
        if (data.UnitOfMeasurerOid == data.UnitsOfMeasure[i].Oid) {
          unit = data.UnitsOfMeasure[i].ShortTitle;
        }
      }
    }
    result = data.UnitOfMeasurerOid === null ? data.DefaultUOM : unit;
    return result;
  }

  selectionChanged(event) {
    console.log('cell', event)
  }

  toOpenNotes(event, data) {
    this.notes = [];
    this.isNotesPopupVisible = true;
    console.log('click', event)
    console.log('data', data)

    let dataSetOidArray = [];
    this.selectedDatasets.map(item => {
      dataSetOidArray.push(item.OID);
    });

    let paramsForNotes = {
        IndicatorOid: data.key,
        IndicatorDataSetOidList: dataSetOidArray
      };
    console.log(' paramsForNotes', paramsForNotes)
    this.standardsService.getIndicatorsNotes(paramsForNotes)
      .subscribe((notes: any[]) => {
        this.notes = notes;
        console.log('notes', this.notes)
      })
  }

  showRowData(event) {
    console.log('ROWDATA', event);
  }

  addMessage(event, itemData) {

    if (this.value !== '') {
      console.log('event', event)
      console.log('value', this.value)
      console.log('data', itemData);
      let paramForAddNote =
        {
          Indicator: itemData.IndicatorOid,
          DataSet: itemData.IndicatorDataSetOid,
          Note: this.value
        };
      this.standardsService.addIndicatorsNotes(paramForAddNote)
        .subscribe(()=> {
          this.testArray1.forEach(indicator => {
            if (indicator.IndicatorOid === itemData.IndicatorOid) {
              indicator.IndicatorNotesCount++;
            }
          });
          console.log('afterAddmessage', this.testArray1)
          itemData.Notes.push({
            Message: this.value,
            Created: new Date(),
            UserFullName: this.currentUserFullName
          });
          this.value = '';

        })
    }
  }

  showGuidance(templateTitle) {
    console.log('templateTitle', templateTitle)
    this.indicatorGuidance = templateTitle.data;
    this.isIndicatorPopupVisible = true;
  }

  selectDatasetsForReview(event) {
    if (this.multySelectorForReview) {
      this.selectedDatasetsForReview = this.multySelectorForReview.selectedItems;
      console.log(' console.log(this.selectedDatasetsForReview)', this.selectedDatasetsForReview);
    }
  }

  upateDatasetsGrade() {
    let dataSetOidArray = [];
    this.selectedDatasetsForReview.map(item => {
      dataSetOidArray.push(item.OID);
    });

    this.indicatorDataSetValueService.updateGradeToUnderReviewState(dataSetOidArray)
      .subscribe(response => {
        console.log('response', response);
        this.userService.showUserNotification('Data Sets was submitted for review', 'success');
        for(let i = 0; i<this.selectedDatasets.length; i++ ){
          dataSetOidArray.forEach(oid => {
            if(this.selectedDatasets[i].OID === oid){
              this.selectedDatasets.splice(i, 1);
              console.log('this.multySelectorForReview.selectedItems', this.multySelectorForReview.selectedItems);
              this.selectedDatasetsForReview = [];
              }
            })
          }

        console.log('this.selectedDatasets', this.selectedDatasets);
        this.isSubmitForReviewPopupVisible = false;
        this.selectedDatasets = [];
        this.indicatorDataSetList = [];
        this.multySelector.value = null;
        console.log('multySelector', this.multySelector);

        this.standardsService.getIndicatorDataSetListByStandardOidFromUnit()
          .subscribe((indicatorDataSetList: any) => {
            console.log("indicatorDataSetList", indicatorDataSetList);
            indicatorDataSetList.map(indicator => {
              this.indicatorDataSetList.push({
                PeriodFrom: `${indicator.PeriodFromMonth < 10 ? '0'+ indicator.PeriodFromMonth : indicator.PeriodFromMonth}-${indicator.PeriodFromYear}/${indicator.PeriodToMonth < 10 ? '0' + indicator.PeriodToMonth : indicator.PeriodToMonth}-${indicator.PeriodToYear}`,
                OID: indicator.OID
              });

            });
            console.log(' this.indicatorDataSetList', this.indicatorDataSetList)
          })
      });
    console.log('dataSetOidArray', dataSetOidArray)
  }
}






